




























import {Component, Vue} from 'vue-property-decorator';
import Slide from "@/js/views/home/PageHeader/Slide.vue";
import SliderNav from "@/js/components/SliderNav.vue";


type ISlide = { text: string; title: string; imgPath: string }

@Component({
    components: {SliderNav, Slide},
    metaInfo: {
        title: '  ',
        meta: [
            {
                vmid: 'description',
                name: 'description',
                content: "   ",
            },
            {
                vmid: 'keywords',
                name: 'keywords',
                content: ', , ,  ,  , ,  ',
            }
        ]
    }
})

export default class PageHeader extends Vue {
    currentSlideIndex = -1;
    slides: Array<ISlide> = [
        {
            imgPath: '/img/town.png',
            text: ' , ,                 .',
            title: '    '
        },
        {
            imgPath: '/img/town.png',
            text: ' , ,                 .',
            title: '    '
        },
        {
            imgPath: '/img/town.png',
            text: ' , ,                 .',
            title: '    '
        },
        {
            imgPath: '/img/town.png',
            text: ' , ,                 .',
            title: '    '
        }
    ];
    idInterval = 0;

    mounted() {
        this.$nextTick(() => {
            this.initSlider()
        })
    }

    prevHandler() {
        clearInterval(this.idInterval);
        this.setInterval();
        if (this.currentSlideIndex === 0)
            this.currentSlideIndex = this.slides.length - 1;
        else
            this.currentSlideIndex--
    }

    nextHandler() {
        clearInterval(this.idInterval);
        this.setInterval();
        if (this.currentSlideIndex === this.slides.length - 1)
            this.currentSlideIndex = 0;
        else
            this.currentSlideIndex++
    }

    setInterval() {
        this.idInterval = setInterval(() => {
            if (this.currentSlideIndex && this.currentSlideIndex + 1 === this.slides.length) {
                this.currentSlideIndex = 0
            } else {
                this.currentSlideIndex++
            }
        }, 5000)
    }

    initSlider() {
        setTimeout(() => {
            this.currentSlideIndex = 0;
            this.setInterval();
        }, 500)
    }
}
